import { Upload, Icon, Modal} from 'antd';
import { connect } from 'react-redux';
import { changeImgName } from '~/actions';

function getBase64(file) {
	return new Promise((resolve, reject) => {
		const reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onload = () => resolve(reader.result);
		reader.onerror = (error) => reject(error);
	});
}
@connect(
	(state) => ({
		...state
	}),
	(dispatch) => ({
		changeImgName:(imgName)=>dispatch(changeImgName(imgName))
	})
)
export default class UpdateImg extends Component {
	state = {
		previewVisible: false,
		previewImage: '',
		fileList: [],
		imgMilddle:[]
	};

	handleCancel = () => this.setState({ previewVisible: false });

	handlePreview = async (file) => {
		if (!file.url && !file.preview) {
			file.preview = await getBase64(file.originFileObj);
		}

		this.setState({
			previewImage: file.url || file.preview,
			previewVisible: true
		});
	};

	handleChange = ({file,fileList})=>{
		const {imgMilddle} =this.state
		const {changeImgName} = this.props
		// console.log(file)
		if(file.status == "done"){
			file.name = file.response.data.name;
			file.url = file.response.data.url
			this.setState({
				imgMildd:imgMilddle.push(file.response.data.name)
			})
		}
		if(file.status == "removed"){
			if(file){
				imgMilddle.splice(imgMilddle.indexOf(file.name),1) 
			}
			this.setState({
				imgMilddle,
			})
		}
		changeImgName(imgMilddle)
		this.setState({
			fileList
		})
	};
	componentWillMount() {
		const { updateGood,imgName } = this.props;
		this.setState({
			fileList: [],
			imgMilddle:imgName
		});
		const BASE_IMG = 'https://tianjiangyang.top:3000/upload/';
		console.log(updateGood);
		let imgs = updateGood.imgs;
		if (imgs) {
			const fileList = imgs.map((img, index) => ({
				uid: -index, // 
				name: img, // 
				status: 'done', // uploading done error removed
				url: BASE_IMG + img
			}));
			this.setState({ fileList });
		}
	}
	render() {
		const { previewVisible, previewImage, fileList } = this.state;
		const uploadButton = (
			<div>
				<Icon type="plus" />
				<div className="ant-upload-text">Upload</div>
			</div>
		);
		return (
			<div className="clearfix">
				<Upload
					action="https://tianjiangyang.top/manage/img/upload"
					name="image"
					listType="picture-card"
					fileList={fileList}
					onPreview={this.handlePreview}
					onChange={this.handleChange}
				>
					{fileList.length >= 3 ? null : uploadButton}
				</Upload>
				<Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
					<img alt="example" style={{ width: '100%' }} src={previewImage} />
				</Modal>
			</div>
		);
	}
}
