

import { EditorState, convertToRaw, ContentState } from 'draft-js'
import { Editor } from 'react-draft-wysiwyg'
import draftToHtml from 'draftjs-to-html'
import htmlToDraft from 'html-to-draftjs'
import { connect } from 'react-redux';
import { changeRichTextEditor } from '~/actions';


@connect(
	(state) => ({
		...state
	}),
	(dispatch) => ({
		getRichTextEditor:(detail)=>dispatch(changeRichTextEditor(detail))
	})
)
export default class RichTextEditor extends Component {
 
  state = {
    editorState: EditorState.createEmpty(),
  }

  onEditorStateChange =(editorState) => {
    const {getRichTextEditor}=this.props
    this.setState({
      editorState,
    });
    const detail = draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))
    getRichTextEditor(detail)
  }

  uploadImageCallBack = (file) => {
    return new Promise(
      (resolve, reject) => {// 
        const xhr = new XMLhttpRequest();
        xhr.open('POST', ' https://tianjiangyang.top/manage/img/upload');
        xhr.setRequestHeader('Authorization', 'Client-ID XXXXX');
        const data = new FormData();
        data.append('image', file);
        xhr.send(data);
        xhr.addEventListener('load', () => {
          const response = JSON.parse(xhr.responseText); // {status:0, data: {name: '', url: '....'}}
          const url = response.data.url
          delete response.data.url
          response.data.link = url
          resolve(response);
        });
        xhr.addEventListener('error', () => {
          const error = JSON.parse(xhr.responseText);
          reject(error);
        });
      }
    );
  }

  componentWillMount () {
    const details = this.props.details
    if (details) {
      // detaileditorState
      const contentBlock = htmlToDraft(details)
      const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks)
      const editorState = EditorState.createWithContent(contentState)
      // 
      this.setState({
        editorState
      })
    }
  }

  getDetail = () => draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))

  render() {
    const { editorState } = this.state;
    return (
      <div>
        <Editor
          editorState={editorState}
          editorStyle={{height: 200, border: '1px solid black', paddingLeft: 10 }}
          onEditorStateChange={this.onEditorStateChange}
          toolbar={{
            image: { uploadCallback: this.uploadImageCallBack, alt: { present: true, mandatory: true } },
          }}
        />
      </div>
    );
  }
}