import './index.scss';
import { Icon, Form, Input, Button, Select,message } from 'antd';
import { axios } from '@/utils/axios';

import UpdateImg from '~/components/updateImg';
import { connect } from 'react-redux';

import RichTextEditor from '~/components/richTextEditor';
import { changeImgName} from '~/actions';

const formItemLayout = {
	labelCol: { span: 2 },
	wrapperCol: { span: 8 }
};
const { TextArea } = Input;
const { Option } = Select;

@connect(
	(state) => ({
		...state
	}),
	(dispatch) => ({
		changeImgName:(imgs)=>dispatch(changeImgName(imgs)),
	})
)
class AddGoods extends Component {
	state = {
		data: []
	};
	handleSubmit = (e) => {
		e.preventDefault();
		// 
		const { richTextEditor, imgName,clearRichTextEditor } = this.props;
		this.props.form.validateFields(async (err, values) => {
			if (!err) {
				const { name, desc, price, categoryId } = values;
				let result
				if (this.isUpdate) {
					 result = await axios.post('/manage/product/update', {_id:this.props.updateGood._id,name, desc, price, categoryId, imgs:imgName, detail:richTextEditor });
				}else{
					 result = await axios.post('/manage/product/add', {name, desc, price, categoryId, imgs:imgName, detail:richTextEditor });
				}
				console.log(result)
				if (result.data.status===0) {
					message.success(`${this.isUpdate ? '' : ''}`)
					this.props.history.push('/index/admin/goodsadmin/home')
				  } else {
					message.error(result.msg)
				  }
			}
		});
	};
	getData = async () => {
		this.setState({
			loading: true
		});
		const result = await axios.get('/manage/category/list');
		this.setState({
			data: result.data.data
		});
		this.setState({ loading: false });
	};
	validatePrice = (rule, value, callback) => {
		if (value === '') {
			callback();
		} else if (value * 1 <= 0) {
			callback('0');
		} else {
			callback();
		}
	};
	componentWillMount() {
		const flag = this.props.updateGood._id;
		this.isUpdate = !!flag;
		if(this.isUpdate){
			const {changeImgName} =this.props
			changeImgName(this.props.updateGood.imgs)
		}
	}
	componentDidMount() {
		this.getData();
	}
	render() {
		const { getFieldDecorator } = this.props.form;
		const { data } = this.state;
		const { updateGood } = this.props;
		return (
			<div className="goodsAdmin">
				<div className="content">
					<div className="contentTop">
						<Icon type="arrow-left" style={{ marginLeft: 20 }} onClick={() => this.props.history.go(-1)} />
						<span> {this.isUpdate ? '' : ''} </span>
					</div>
					<div className="contentBottom">
						<Form {...formItemLayout} onSubmit={this.handleSubmit}>
							<Form.Item label="">
								{getFieldDecorator('name', {
									initialValue: updateGood.name || '',
									rules: [
										{
											required: true,
											message: ''
										}
									]
								})(<Input placeholder="" />)}
							</Form.Item>
							<Form.Item label="">
								{getFieldDecorator('desc', {
									initialValue: updateGood.desc || '',
									rules: [
										{
											required: true,
											message: ''
										}
									]
								})(<TextArea placeholder="" autosize={{ minRows: 5, maxRows: 6 }} />)}
							</Form.Item>
							<Form.Item label="">
								{getFieldDecorator('price', {
									initialValue: updateGood.price || '',
									rules: [
										{
											required: true,
											message: '0'
										},
										{ validator: this.validatePrice }
									]
								})(<Input prefix="" type="number" suffix="" />)}
							</Form.Item>
							<Form.Item label="">
								{getFieldDecorator('categoryId', {
									initialValue: updateGood.categoryId || '',
									rules: [
										{
											required: true,
											message: ''
										}
									]
								})(
									<Select style={{ width: 610 }} placeholder="">
										{data.map((item, i) => (
											<Option value={item._id} key={i}>
												{' '}
												{item.name}{' '}
											</Option>
										))}
									</Select>
								)}
							</Form.Item>
							<Form.Item label="">
								<UpdateImg />
							</Form.Item>
							<Form.Item label="" wrapperCol={{ span: 18 }}>
								<RichTextEditor details={updateGood.detail} />
							</Form.Item>
							<Form.Item wrapperCol={{ span: 12, offset: 5 }}>
								<Button type="primary" htmlType="submit">
									
								</Button>
							</Form.Item>
						</Form>
					</div>
				</div>
			</div>
		);
	}
}

export default Form.create()(AddGoods);
